import {mapMutations} from "vuex";

export default {
  name: "events",
  mounted() {
    this.setVisitIn('events');
    this.setBackgroundColor('#FAFAFA');
  },
  methods: {
    ...mapMutations({
      setBackgroundColor: 'setBackgroundColor',
      setVisitIn: 'setVisitIn'
    })
  },
  head() {
    return {
      title: 'events',
      meta: [
        {
          hid: 'title',
          name: 'title',
          content: 'events | ヒナクラ'
        },
        {
          hid: 'description',
          name: 'description',
          content: '開催されたイベントの一覧です。'
        },
        {
          hid: 'og:url',
          property: 'og:url',
          content: `${process.env.BASE_URL / this.$route.fullPath}`,
        },
        {
          hid: 'og:type',
          property: 'og:type',
          content: 'article',
        },
        {
          hid: 'og:title',
          property: 'og:title',
          content: 'events | ヒナクラ',
        },
        {
          hid: 'og:image',
          property: 'og:image',
          content: `${process.env.BASE_URL}/img/logos/hnkr.png`,
        },
        {
          hid: 'og:description',
          property: 'og:description',
          content: '開催されたイベントの一覧です。',
        }
      ]
    }
  }
}
